//     (icon  )
export function renderCategoryItem(category, isDesktop = false) {
  if (isDesktop) {
    return (
      <div className="flex flex-col items-center bg-white rounded-xl shadow-lg p-6 cursor-pointer transition-all hover:shadow-xl hover:scale-105 border border-gray-100">
        <div className="mb-4 p-4 bg-gradient-to-br from-blue-50 to-indigo-50 rounded-full">
          {category.iconComponent}
        </div>
        <h3 className="text-lg font-semibold text-gray-800 mb-2 text-center">{category.name}</h3>
        <p className="text-sm text-gray-600 text-center">{generateCategoryDescription(category.name)}</p>
      </div>
    );
  }
  
  // 
  return (
    <div className="flex flex-col items-center bg-white rounded-xl shadow p-3 cursor-pointer transition-all hover:shadow-lg">
      <div className="mb-2">
        {category.iconComponent}
      </div>
      <span className="text-xs font-semibold text-gray-700 text-center">{category.name}</span>
    </div>
  );
}

