import React, { useState } from "react";
import { useNavigate } from "react-router-dom";

export default function SignupPage() {
  const [userId, setUserId] = useState("");
  const [password, setPassword] = useState("");
  const [password2, setPassword2] = useState("");
  const [email, setEmail] = useState("");
  const navigate = useNavigate();

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!userId || !password || !password2 || !email) {
      alert("  .");
      return;
    }
    if (password !== password2) {
      alert("  .");
      return;
    }
    try {
      const res = await fetch("https://kosa-backend-315281980252.asia-northeast3.run.app/api/signup", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ userId, password, email })
      });
      if (res.ok) {
        alert(" !  .");
        navigate("/login");
      } else {
        const data = await res.json();
        alert(data.message || " ");
      }
    } catch (err) {
      alert(" : " + err.message);
    }
  };

  return (
    <div className="min-h-screen flex items-center justify-center bg-gray-100">
      <form
        onSubmit={handleSubmit}
        className="bg-white p-8 rounded-lg shadow-md w-full max-w-sm"
      >
        <div className="flex flex-col items-center mb-8">
          <span className="text-4xl font-extrabold text-blue-600 tracking-widest">KOSA</span>
        </div>
        <h2 className="text-2xl font-bold mb-6 text-center"></h2>
        <div className="mb-4">
          <label className="block mb-1 text-gray-700"> ID</label>
          <input
            type="text"
            className="w-full px-3 py-2 border rounded focus:outline-none focus:ring"
            value={userId}
            onChange={(e) => setUserId(e.target.value)}
            placeholder=" "
            autoFocus
          />
        </div>
        <div className="mb-4">
          <label className="block mb-1 text-gray-700"></label>
          <input
            type="email"
            className="w-full px-3 py-2 border rounded focus:outline-none focus:ring"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            placeholder=" "
          />
        </div>
        <div className="mb-4">
          <label className="block mb-1 text-gray-700"></label>
          <input
            type="password"
            className="w-full px-3 py-2 border rounded focus:outline-none focus:ring"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            placeholder=" "
          />
        </div>
        <div className="mb-6">
          <label className="block mb-1 text-gray-700"> </label>
          <input
            type="password"
            className="w-full px-3 py-2 border rounded focus:outline-none focus:ring"
            value={password2}
            onChange={(e) => setPassword2(e.target.value)}
            placeholder="  "
          />
        </div>
        <button
          type="submit"
          className="w-full bg-blue-600 text-white py-2 rounded hover:bg-blue-700 transition"
        >
          
        </button>
        <div className="flex justify-center mt-4">
          <button
            type="button"
            className="text-blue-600 hover:underline"
            onClick={() => navigate('/login')}
          >
              ? 
          </button>
        </div>
      </form>
    </div>
  );
} 